 <template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">运维详情</span>
    </mu-appbar> -->
    <van-nav-bar
      title="运维详情"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="add" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="body">
      <div class="contentBody">
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>站点</span>
          </div>
          <div style="flex:5">
            <span>{{list.station}}</span>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>运维时间</span>
          </div>
          <div style="flex:5">
            <span>{{list.gmtCreate}}</span>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>运维人员</span>
          </div>
          <div style="flex:5">
            <span>{{list.person}}</span>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>问题</span>
          </div>
          <div style="flex:5">
            <span>{{list.title}}</span>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>问题状态</span>
          </div>
          <div style="flex:5">
            <span v-show="list.status===0" style="color:red">未解决</span>
            <span v-show="list.status===1" style="color:green">已解决</span>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>问题详情</span>
          </div>
          <!-- <div style="flex:5">
            <span>{{list.content}}</span>
          </div> -->
        </div>
        <div class="">
           <div style="flex:5">
            <textarea class="textarea" :value="list.content"></textarea>
          </div>
        </div>
        <div class="boxFlex">
          <div style="flex:2;color:grey">
            <span>问题图片</span>
          </div>
        </div>
        <div class="boxFlexImage flexCenter" v-show="list.problemImage==''||list.problemImage==null">
          <span style="color:grey">暂无图片</span>
        </div>
        <div class="boxFlexImage" v-show="list.problemImage!==''&&list.problemImage!==null">
          <div
            v-for="(img,i) in list.problemImage"
            :key="i"
            style="margin-right:10px;margin-top:10px;"
            class="imageSize"
          >
            <!-- <van-image width="5rem" height="5rem" fit="cover" :src="urlImage+img" /> -->
            <img :src="urlImage+img" alt @click="viewImage(urlImage+img)" />
          </div>
        </div>
      </div>
    </div>
    <div
      v-show="status===1"
      class="contentBody"
      style="padding-top:0;border-top:10px solid #f5f5f5"
    >
      <div class="boxFlex">
        <div style="flex:2;color:grey">
          <span>解决时间</span>
        </div>
        <div style="flex:5">{{list.gmtFinish}}</div>
      </div>
      <div class="boxFlex">
        <div style="flex:2;color:grey">
          <span>解决人员</span>
        </div>
        <div style="flex:5">{{list.finishPerson}}</div>
      </div>
      <div class="boxFlex">
        <div style="flex:2;color:grey">
          <span>解决方案</span>
        </div>
        <!-- <div style="flex:5">{{list.solution}}</div> -->
      </div>
      <div class="">
           <div style="flex:5">
            <textarea class="textarea" :value="list.solution"></textarea>
          </div>
        </div>
      <div class="boxFlex">
        <div style="flex:2;color:grey">
          <span>解决图片</span>
        </div>
      </div>
      <div class="boxFlexImage flexCenter" v-show="list.solutionImage==''||list.solutionImage==null">
        <span style="color:grey">暂无图片</span>
      </div>
      <div class="boxFlexImage" v-show="list.solutionImage!==''&&list.solutionImage!==null">
        <div
          v-for="(img1,i1) in list.solutionImage"
          :key="i1"
          style="margin-right:10px;margin-top:10px;"
          class="imageSize"
        >
          <!-- <van-image width="5rem" height="5rem" fit="cover" :src="urlImage+img" /> -->
          <img :src="urlImage+img1" alt @click="viewImage(urlImage+img1)" />
        </div>
      </div>
    </div>
    <div v-show="status===0" style="margin-top:30px;" class="submit">
      <div class="bottomSubmit" @click="submit()">
        <div class="btnSubmit backgroundNaviBar">
          <span>提交解决</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { UrlPic } from "../../../static/js/requestUrl";
import Vue from "vue";
import { Image } from "vant";
// import Vue from 'vue';
import { ImagePreview } from "vant";

Vue.use(ImagePreview);
Vue.use(Image);
export default {
  data() {
    return {
      list: [],
      status: 0,
      urlImage: ""
    };
  },
  created() {
    this.urlImage = UrlPic;
  },
  mounted() {
    this.list = this.$store.state.controlContent;
    if (
      this.$route.query.name !== null &&
      this.$route.query.name !== "" &&
      this.$route.query.name !== undefined
    ) {
      this.$store.state.status = this.$route.query.name;
    }

    this.status = Number(this.$store.state.status);
    // debugger
    var imgPath = this.list.problemImage;
  //   debugger
    var imagePath = this.list.solutionImage;
    // debugger
    if (imgPath !== "" && imgPath !== null) {
      this.list.problemImage = imgPath.split(",");
    }
    if (imagePath !== "" && imagePath !== null) {
      this.list.solutionImage = imagePath.split(",");
    }
  //  debugger
  },
  methods: {
    viewImage(img) {
      ImagePreview([img]);
    },
    submit() {
      this.$router.push({
        path: "/SubmitControl"
      });
    }
  }
};
</script>
<style  scoped>
.body {
}
.contentBody {
  background-color: white;
  padding: 10px;
}
.boxFlex {
  display: flex;
  flex-direction: row;
  height: 30px;
  align-items: center;
}
.boxFlexImage {
  display: flex;
  flex-direction: row;
  /* height: 30px; */
  align-items: center;
}
.submit {
}
.bottomSubmit {
  display: flex;
  justify-content: center;
}
.btnSubmit {
  height: 40px;
  color: white;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96%;
  margin-top: 10px;
}
.imageSize img {
  width: 80px;
  height: 80px;
}
.flexCenter{
  justify-content: center;
  height: 40px;
}
.textarea{
  width: 100%;
  height: 100px;
  border: none;
  border-radius: 5px;
  border: 1px rgb(236, 236, 236) solid;
}
</style>


